import React, { useEffect } from "react";

import { graphql, Link } from "gatsby";
import { Helmet } from 'react-helmet';

import Layout from "../components/layout_inner";
import CoverImg from "../components/cover_img";

export const query = graphql`
  query CategoryImages($categoryDir: String!) {
    categories: allFile(
      filter: {
        relativeDirectory: { eq: $categoryDir }
      },
      sort: { fields: ctime }
    ) {
      edges {
        node {
          name
          childImageSharp {
            fluid(quality: 100, maxWidth: 1920) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`

export default ({ data, pageContext }) => {
  useEffect(() => {
    // workaround glightbox not supporting ssr
    import('glightbox').then(GLightbox => {
      GLightbox({
        selector: 'a.photo',
        type: 'image'
      });
    });
  });

  return (
    <Layout>
      <Helmet title={pageContext.title} defer={false} />
      <h1>{pageContext.title}</h1>
      <p>&nbsp;</p>
      <div className="cards">
        {data.categories.edges.map(({ node }) => (
          <Link
            key={node.name}
            to={!pageContext.inner ? `/gallery/${pageContext.title}/${node.name}` : node.childImageSharp.fluid.src}
            className={`card ${pageContext.inner ? 'photo' : ''}`}
            style={{ margin: '1.5% 0 1.5% 3%' }}
          >
            <span className="thumbnail">
              <CoverImg fluid={node.childImageSharp.fluid} alt={node.name} />
            </span>
            {!pageContext.inner ? <span className="title">{node.name}</span> : ''}
          </Link>
        ))}
      </div>
      <div style={{ paddingBottom: "1.5%" }} className="clear"></div>
    </Layout>
  );
};
